import React, { memo } from "react";
import PropTypes from "prop-types";
import { BASE_URL } from "../../constants/app.constants";
import "./index.scss";

const MovieCard = ({ imageUrl, title }) => {
  const url = `${BASE_URL}images/${imageUrl}`;
  
  // this is just a placeholder image instead of loading broken image.
  const placeHolderImage = '/images/no-image-placeholder.jpg';

  const onImageError = (e) => {
    e.target.src = placeHolderImage
  }
  
  return (
    <div className="movie-card">
      <img className="movie-card--image" src={url} alt={title} loading="lazy"  onError={onImageError} />
      <div className="movie-card--title">
        <span>{title}</span>
      </div>
    </div>
  );
};

MovieCard.propTypes = {
  imageUrl: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
};

export default memo(MovieCard);
